import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom';
import { MENU_ITEMS, THEME_DARK, THEME_LIGHT } from '../../constants/Constants';
import { auth } from '../Auth/Auth';
import { LiliContext } from '../../context/LiliContext';
import './Nav.scss';

class Nav extends Component {
  // contextType lÃ  1 biáº¿n cá»§a React.Component,
  // sau lá»‡nh gÃ¡n nÃ y ta cÃ³ thá»ƒ dÃ¹ng Ä‘Æ°á»£c data cá»§a LiliContext thÃ´ng qua: this.context
  static contextType = LiliContext;

  handleLogout = () => {
    auth.logout();
  };

  getActiveMenuItems = (roleArray, menuItems) => {
    menuItems.forEach((item, index) => {
      if (item.subItems) {
        this.getActiveMenuItems(roleArray, item.subItems);
      } else {
        if (!auth.rolesHasPermission(roleArray, item.path)) {
          // Äá»ªNG dÃ¹ng splice, bá»Ÿi vÃ¬ JS báº¥t Ä‘á»“ng bá»™ nÃªn sáº½ bá»‹ lá»—i thá»‰nh thoáº£ng bá»‹ miss menu item
          // menuItems.splice(index, 1);
          item.enabled = false;
        }
      }
    });
  };

  generateMenu = (menuItems) => {
    const { pathname } = this.props.location;
    return menuItems.map((item) => {
      const itemClass = 'menu-item' + (item.path === pathname ? ' active-menu' : '');
      if (item.path === null) item.path = '#';

      if (item.subItems) {
        return (
          <li key={item.key} className={itemClass + ' menu-parent level' + item.level}>
            <Link to={item.path}>
              {item.name} <i className={'caret fas ' + (item.level > 1 ? 'fa-caret-right' : 'fa-caret-down')}></i>
            </Link>
            <ul key={item.key} className={itemClass + ' sub-menu level' + (item.level + 1)}>
              {this.generateMenu(item.subItems)}
            </ul>
          </li>
        );
      } else {
        return (
          item.enabled && (
            <li key={item.key} className={itemClass + ' level' + item.level}>
              <Link to={item.path}>{item.name}</Link>
            </li>
          )
        );
      }
    });
  };

  toggleDarkMode = () => {
    const { theme, updateTheme } = this.context;
    if (theme === THEME_LIGHT) {
      updateTheme(THEME_DARK);
    } else {
      updateTheme(THEME_LIGHT);
    }
  };

  render() {
    const { userInfo } = this.props;
    const roleArray = userInfo ? userInfo.roleArray : [];
    const menuItems = [...MENU_ITEMS];
    const { theme } = this.context;
    this.getActiveMenuItems(roleArray, menuItems);

    return (
      <nav className="custom-navbar">
        <ul className="nav-wrapper">{this.generateMenu(menuItems)}</ul>
        <div className="userinfo-wrapper">
          <div className="div-header">
            <span
              className="btn_dark_mode"
              title={theme === THEME_LIGHT ? 'Toggle Dark mode' : 'Toggle Light mode'}
              onClick={this.toggleDarkMode}
              role="img"
              aria-label="emoji"
            >
              {theme === THEME_LIGHT ? 'ðŸŒœ' : 'ðŸŒž'}
            </span>
          </div>
        </div>
      </nav>
    );
  }
}

// Use withRouter HOC in order to inject match, history and location in your component props.
export default withRouter(Nav);
