import { ACCESS_TOKEN } from '../../constants/Constants';
import axiosClient from '../../service/axiosClient';
import Toast from '../Toast/Toast';
import { ROLE_TABLE } from '../../constants/Constants';

class Auth {
  constructor() {
    this.userInfo = null;
  }

  isAuthenticated = () => {
    const access_token = localStorage.getItem(ACCESS_TOKEN);
    return !!access_token;
  };

  login = ({ username, password }, successCallback, failCallback) => {
    const data = {
      username,
      password
    };
    axiosClient
      .post('/signin', data)
      .then((res) => {
        localStorage.setItem(ACCESS_TOKEN, res[ACCESS_TOKEN]);
        successCallback();
      })
      .catch((err) => {
        failCallback(err.data ? err.data : err);
      });
  };

  logout = () => {
    axiosClient
      .post('/signout')
      .then((res) => {
        localStorage.removeItem(ACCESS_TOKEN);
        window.location = '/';
      })
      .catch((err) => {
        console.log(err);
        Toast.error(err);
      });
  };

  getMe = () => {
    return axiosClient.get('/api/user/me');
  };

  redirectToLoginPage = () => {
    Toast.info('You need to login first!');
    localStorage.removeItem(ACCESS_TOKEN);
    window.location.hash = '/login';
  };

  /**
   * Check xem máº£ng roles cÃ³ tá»“n táº¡i role nÃ o cÃ³ quyá»n truy cáº­p path hay ko
   * @param {string[]} roles - Array of role
   * @param {string} path - URL
   */
  rolesHasPermission = (roles, path) => {
    if (!ROLE_TABLE[path]) return true;
    for (let i = 0; i < roles.length; i++) {
      if (ROLE_TABLE[path].includes(roles[i])) return true;
    }
    // DÃ¹ng forEach: báº¥t Ä‘á»“ng bá»™ nÃªn ko Ä‘Æ°á»£c nhÃ©!
    // NÃ³ sáº½ return false trÆ°á»›c khi cháº¡y vÃ o trong forEach
    // roles.forEach(role => {
    //   if (ROLE_TABLE[path].includes(role)) return true;
    // });
    return false;
  };
}

export let auth = new Auth();
